// src/hooks/LanguageContext.tsx

import React, { createContext, useContext, useState, ReactNode, FC } from 'react';
import { Language } from '../types'; 

//  renderTextWithBreaks  ! (  src/utils/textUtils.tsx   .)

// 1. Context  
interface LanguageContextType {
 language: Language;
 toggleLanguage: () => void;
 t: (key: string) => string;
}

// 2. Context 
const LanguageContext = createContext<LanguageContextType | undefined>(undefined);

// 3.    (   '\n'  )
const translations: { [key: string]: { [lang in Language]: string } } = {
  'welcome_message': { ko: '25 .\n.', en: 'Welcome to\n 25th Street Coffee.'},
  'admin_mode': { ko: '', en: 'Admin' },
  'store': { ko: '', en: 'Store' },
  'takeout': { ko: '', en: 'Takeout' },
};

// 4. Provider  
export const LanguageProvider: FC<{ children: ReactNode }> = ({ children }) => {
 const [language, setLanguage] = useState<Language>('ko');

 const toggleLanguage = () => {
  setLanguage(prev => (prev === 'ko' ? 'en' : 'ko'));
 };

 const t = (key: string): string => {
  // t()    . JSX   .
  return translations[key]?.[language] || key;
 };

 const contextValue = { language, toggleLanguage, t };

 return ( 
  <LanguageContext.Provider value={contextValue}>
   {children}
  </LanguageContext.Provider>
 );
};

// 5.  
export const useLanguage = () => {
 const context = useContext(LanguageContext);
 if (!context) {
  throw new Error('useLanguage LanguageProvider   .');
 }
 return context;
};