// src/pages/MainScreen.tsx

import React from 'react';
import MenuArea from '../components/MenuArea';
import OrderArea from '../components/OrderArea';
import OptionModal from '../components/OptionModal';
//  src/hooks/usePosSystem.ts    
import { usePosSystem } from '../hooks/usePosSystem'; 
import '../styles/main.css'; 
import { MainScreenProps } from '../types';




//  [ ] MainScreenProps   Props . 
export const MainScreen: React.FC<MainScreenProps> = ({ 
    navigateTo, // App.tsx  
    orderType: orderType, // App.tsx   
    // totalPrice    ,     .
}) => {
    // usePosSystem  POS     .
    const {
        currentOrder,
        selectedItemId,
        isModalOpen,
        modalItemData,
        grandTotal,
        handleItemClick,
        handleAddToOrder,
        handleQtyChange,
        handleDeleteItem,
        handleOrderItemSelect,
        setIsModalOpen
    } = usePosSystem(); 
    
    return (
        // POS    
        <div id="pos-container"> 
            <MenuArea onItemClick={handleItemClick} />
            
            <OrderArea 
                currentOrder={currentOrder} 
                grandTotal={grandTotal} 
                selectedItemId={selectedItemId}
                onItemSelect={handleOrderItemSelect}
                onQtyChange={handleQtyChange}
                onDelete={handleDeleteItem}
                navigateTo={navigateTo}
                orderType={orderType}
            />
            
            {isModalOpen && modalItemData && (
                <OptionModal
                    initialItem={modalItemData}
                    onClose={() => setIsModalOpen(false)}
                    onConfirm={handleAddToOrder}
                />
            )}
        </div>
    );
};