import React, { useState } from 'react';
import styled from 'styled-components';
import { COLORS } from '../../../theme/colorPalette'; //   :      (../theme -> ../../theme)
import { StaffForm, PermissionRole } from '../../../types/admin'; //   :      (../types -> ../../types)

// ---   (   ) ---
// *Note:   StaffEditModal        , 
//    .*

//   
export const ModalOverlay = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
`;

//  
export const ModalContent = styled.div`
    background-color: ${COLORS.BACKGROUND_LIGHT};
    padding: 30px;
    border-radius: 12px;
    width: 450px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
`;

export const ModalTitle = styled.h2`
    color: ${COLORS.PRIMARY_DARK};
    margin-bottom: 25px;
    border-bottom: 2px solid ${COLORS.BACKGROUND_MEDIUM};
    padding-bottom: 10px;
`;

//  
export const FormGroup = styled.div`
    margin-bottom: 20px;
`;

export const Label = styled.label`
    display: block;
    color: ${COLORS.TEXT_DARK};
    font-weight: bold;
    margin-bottom: 8px;
`;

export const Input = styled.input`
    width: 100%;
    padding: 10px;
    border: 1px solid ${COLORS.BACKGROUND_DARK};
    border-radius: 6px;
    font-size: 1rem;
    color: ${COLORS.TEXT_DARK};
    background-color: ${COLORS.BACKGROUND};
    &:focus {
        border-color: ${COLORS.ACCENT};
        outline: none;
    }
`;

export const Select = styled.select`
    width: 100%;
    padding: 10px;
    border: 1px solid ${COLORS.BACKGROUND_DARK};
    border-radius: 6px;
    font-size: 1rem;
    color: ${COLORS.TEXT_DARK};
    background-color: ${COLORS.BACKGROUND_LIGHT};
    cursor: pointer;
`;

//  
export const ButtonGroup = styled.div`
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 30px;
`;

export const CancelButton = styled.button`
    padding: 10px 20px;
    background-color: ${COLORS.TEXT_MUTED};
    color: ${COLORS.TEXT_LIGHT};
    border: none;
    border-radius: 8px;
    cursor: pointer;
    &:hover {
        background-color: ${COLORS.TEXT_DARK};
    }
`;

export const SaveButton = styled.button`
    padding: 10px 20px;
    background-color: ${COLORS.ACCENT};
    color: ${COLORS.TEXT_LIGHT};
    border: none;
    border-radius: 8px;
    cursor: pointer;
    &:disabled {
        background-color: ${COLORS.BACKGROUND_DARK};
        cursor: not-allowed;
    }
    &:hover:not(:disabled) {
        background-color: ${COLORS.ACCENT_DARK};
    }
`;

// ---   ---

interface StaffAddModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSave: (formData: StaffForm) => void;
}

export const StaffAddModal: React.FC<StaffAddModalProps> = ({ isOpen, onClose, onSave }) => {
    
    // StaffForm   .
    const [formData, setFormData] = useState<StaffForm>({
        name: '',
        employeeId: '',
        password: '',
        role: 'Staff', //   'Staff' 
    });

    const permissionRoles: PermissionRole[] = ['Admin', 'Manager', 'Staff'];

    //    
    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
        const { name, value } = e.target;
        setFormData(prev => ({ //  TS7006  
            ...prev,
            [name]: value,
        }));
    };

    //    
    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        
        //    
        if (!formData.name || !formData.employeeId || !formData.password) {
            alert('    .');
            return;
        }
        
        onSave(formData);
        setFormData({ name: '', employeeId: '', password: '', role: 'Staff' }); //  
    };

    if (!isOpen) {
        return null;
    }

    return (
        <ModalOverlay onClick={onClose}>
            <ModalContent onClick={e => e.stopPropagation()}>
                <ModalTitle>  </ModalTitle>
                <form onSubmit={handleSubmit}>
                    <FormGroup>
                        <Label htmlFor="name"> </Label>
                        <Input
                            id="name"
                            name="name"
                            type="text"
                            value={formData.name}
                            onChange={handleChange}
                            placeholder=": "
                        />
                    </FormGroup>

                    <FormGroup>
                        <Label htmlFor="employeeId"> ( ID)</Label>
                        <Input
                            id="employeeId"
                            name="employeeId"
                            type="text"
                            value={formData.employeeId}
                            onChange={handleChange}
                            placeholder="  "
                        />
                    </FormGroup>

                    <FormGroup>
                        <Label htmlFor="password"> </Label>
                        <Input
                            id="password"
                            name="password"
                            type="password"
                            value={formData.password}
                            onChange={handleChange}
                            placeholder="  "
                        />
                    </FormGroup>

                    <FormGroup>
                        <Label htmlFor="role"> </Label>
                        <Select
                            id="role"
                            name="role"
                            value={formData.role}
                            onChange={handleChange}
                        >
                            {permissionRoles.map(role => (
                                <option key={role} value={role}>{role}</option>
                            ))}
                        </Select>
                    </FormGroup>

                    <ButtonGroup>
                        <CancelButton type="button" onClick={onClose}></CancelButton>
                        <SaveButton type="submit"></SaveButton>
                    </ButtonGroup>
                </form>
            </ModalContent>
        </ModalOverlay>
    );
};