// src/types/index.ts

export type ScreenState = 'START' | 'MAIN' | 'PAYMENT' | 'COMPLETE' | 'ADMIN';
export type OrderType = 'STORE' | 'TAKEOUT';
export type Language = 'ko' | 'en';



//    
export type Modifier = string; // : " ", " UP"

//     
export interface OrderItem {
    id: string;         //   ID (uniqueId)
    baseId: string;     //   ID (MENU_ITEMS_DATA key)
    name: string;
    price: number;      //  
    qty: number;
    temp: 'HOT' | 'ICE';
    modifiers: Modifier[];
    memo: string;
}

//    
export interface MenuItem {
    id: string;
    name: string;
    price: number;
    category: Category;
}

//  
export type Category = "COFFEE" | "BEVERAGE" | "TEA" | "FRAPPUCCINO" | "AED";

//   OrderItem  key-value  
export interface CurrentOrder {
    [key: string]: OrderItem;
}

//       (OrderIte id )
export type InitialItemData = Omit<OrderItem, 'id'>;

export interface PaymentScreenProps {
    //   :  (screen)  (props) 
    navigateTo: (screen: ScreenState, props?: any) => void; 
    
    //    (MainScreen )
    orderItems: OrderItem[]; 
    
    //     (  )
    subTotal: number; 
    
    // (STORE)  (TAKEOUT) 
    orderType: 'STORE' | 'TAKEOUT';
}

export const ScreenStates = {
    START: 'START' as ScreenState,
    MAIN: 'MAIN' as ScreenState,
    PAYMENT: 'PAYMENT' as ScreenState,
    COMPLETE: 'COMPLETE' as ScreenState,
    ADMIN: 'ADMIN' as ScreenState,
};

export interface MainScreenProps {
    // navigateTo   MainScreen  .
    navigateTo: (screen: ScreenState, props?: any) => void;
    
    // MainScreen   props .
    currentOrder: CurrentOrder;
    orderType: 'STORE' | 'TAKEOUT';
    totalPrice: number;
    // ...   props
}

//  []   (CompleteScreen)  Props  
export interface CompleteScreenProps {
    //   
    navigateTo: (screen: ScreenState, props?: any) => void; 
    
    //      
    orderId: string; 
    
    // /  (  )
    orderType: 'STORE' | 'TAKEOUT';
    
    // ... (       .)
}

//  [ ]   (Transaction)   
export interface Transaction {
    id: string; // Firestore  ID (  ID)
    orderId: string; //    
    orderType: OrderType; // /
    
    //   (  )
    status: 'COMPLETED' | 'CANCELED' | 'REFUNDED'; // , , 
    
    //   (   )
    timestamp: Date; //    (Date   Firestore Timestamp)
    
    //  
    totalAmount: number; //   
    paymentMethod: 'CARD' | 'CASH' | 'QR_PAY'; //  
    
    //    (    )
    items: OrderItem[]; 
}

//  [] AdminScreenProps  
export interface AdminScreenProps {
    //    .
    navigateTo: (screen: ScreenState, props?: any) => void; 
}


export interface ReportCardProps {
    title: string;
    value: string | number;
    icon?: React.ReactNode; 
}

export interface TransactionTableProps {
    transactions: Transaction[];
    onRefund: (transactionId: string) => void;
    onTransactionClick: (transaction: Transaction) => void;
}

