// src/components/TransactionTable.tsx

import React from 'react';
import styled from 'styled-components';
import { COLORS } from '../theme/colorPalette';
// types/index.ts Transaction  . (    import )
import { Transaction, TransactionTableProps } from '../types';
import { formatPrice } from '../utils/helpers';


// ---   ---
const Table = styled.table`
    width: 100%;
    border-collapse: collapse;
    font-size: 1rem;
    table-layout: fixed; 
`;

const Thead = styled.thead`
    background-color: ${COLORS.PRIMARY_DARK};
    color: ${COLORS.TEXT_LIGHT};
    position: sticky; 
    top: 0;
    z-index: 10;
`;

const Th = styled.th`
    padding: 15px 10px;
    text-align: left;
    font-weight: 700;
    border-bottom: 2px solid ${COLORS.PRIMARY};
`;

const Tbody = styled.tbody``;

const Tr = styled.tr`
    border-bottom: 1px solid ${COLORS.BACKGROUND_DARK}; 

    &:nth-child(even) {
        background-color: ${COLORS.BACKGROUND}; // F5F5F5
    }
    &:hover {
        background-color: ${COLORS.BACKGROUND_DARK}20; //  
    }
    cursor: pointer; /*       */
  border-bottom: 1px solid ${COLORS.BACKGROUND_DARK};
`;

const Td = styled.td`
    padding: 15px 10px;
    vertical-align: middle;
`;

const RefundButton = styled.button`
    background-color: ${COLORS.DANGER}; // DANGER  
    color: ${COLORS.TEXT_LIGHT};
    border: none;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.2s;

    &:hover {
        background-color: ${COLORS.DANGER_DARK};
    }
    &:disabled {
        background-color: ${COLORS.BACKGROUND_DARK}; //  
        cursor: not-allowed;
    }
        
`;

const ReceiptButton = styled.button`
    background-color: ${COLORS.SECONDARY}; /*  ( ) */
    color: ${COLORS.TEXT_DARK};
    border: none;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.2s;
    margin-right: 10px; /*    */
    
    &:hover {
        background-color: ${COLORS.SECONDARY_DARK};
    }
    &:disabled {
        background-color: ${COLORS.BACKGROUND_DARK};
        cursor: not-allowed;
        color: ${COLORS.TEXT_MUTED};
    }
`;

// ---   ---
export const TransactionTable: React.FC<any> = ({ 
    transactions, 
    onRefund,
    onTransactionClick,
    onReprintReceipt //   Prop    <any> .
}) => {

    //           
    const handleRefundClick = (e: React.MouseEvent, transactionId: string) => {
        e.stopPropagation(); //          . 
        onRefund(transactionId);
    };

    //       (    !) 
    const handleReprintClick = (e: React.MouseEvent, transactionId: string) => {
        e.stopPropagation(); //     
        onReprintReceipt(transactionId);
    };

    //       
    const getStatusStyle = (status: Transaction['status']) => {
        switch (status) {
            case 'REFUNDED': return { color: COLORS.DANGER, fontWeight: 'bold' };
            case 'COMPLETED': return { color: COLORS.PRIMARY, fontWeight: 'bold' };
            case 'CANCELED': return { color: COLORS.TEXT_DARK };
            default: return {};
        }
    };
    
    //  (Date )   .
    const formatTime = (timestamp: Date): string => {
        return timestamp.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' });
    };

    return (
        <Table>
            <Thead>
                <Tr style={{ backgroundColor: COLORS.PRIMARY }}>
                    <Th style={{ width: '10%' }}></Th>
                    <Th style={{ width: '15%' }}></Th>
                    <Th style={{ width: '20%' }}> </Th>
                    <Th style={{ width: '15%' }}> </Th>
                    <Th style={{ width: '15%' }}></Th>
                    <Th style={{ width: '25%', textAlign: 'center' }}></Th>
                </Tr>
            </Thead>
            <Tbody>
                {transactions.map((tx: Transaction) => (
                    <Tr 
                        key={tx.id} 
                        onClick={() => onTransactionClick(tx)}
                    >
                        <Td>{tx.orderId}</Td>
                        <Td>{formatTime(tx.timestamp)}</Td>
                        <Td>{formatPrice(tx.totalAmount)}</Td>
                        <Td>{tx.paymentMethod}</Td>
                        <Td>
                            <span style={getStatusStyle(tx.status)}>{tx.status}</span>
                        </Td>
                        <Td style={{ textAlign: 'center' }}>                            
                          {/*  1.     */}
                            <ReceiptButton 
                                onClick={(e) => handleReprintClick(e, tx.id)}
                                //      (   )
                                disabled={tx.status === 'REFUNDED'} 
                            >
                                 
                            </ReceiptButton>

                            {/*  2. /   */}
                            <RefundButton 
                                onClick={(e) => handleRefundClick(e, tx.id)}
                                disabled={tx.status !== 'COMPLETED'} 
                            >
                                {tx.status === 'COMPLETED' ? '/' : ' '}
                            </RefundButton>
                        </Td>
                    </Tr>
                ))}
            </Tbody>
        </Table>
    );
};