// src/hooks/useDeviceStatus.ts

import { useState, useEffect, useCallback } from 'react';

//    
export type DeviceStatus = 'OK' | 'ERROR' | 'WARN' | 'UNKNOWN';

export interface SystemStatus {
    label: string;
    status: DeviceStatus;
    detail: string;
}

export interface DeviceCheckData {
    network: SystemStatus[];
    payment: SystemStatus[];
    printer: SystemStatus[];
    system: SystemStatus[];
}

const initialData: DeviceCheckData = {
    network: [],
    payment: [],
    printer: [],
    system: [],
};

const API_BASE_URL = 'http://localhost:3001'; //

//  API        
const fetchCheckDevice = async (): Promise<DeviceCheckData> => {
    // 1.   
    await new Promise(resolve => setTimeout(resolve, 1500)); 

    let posServerStatus: DeviceStatus = 'ERROR';
    let posServerDetail = '  ';

    // 2. POS     ( API : /transactions  )
    try {
        const response = await fetch(`${API_BASE_URL}/transactions`);
        if (response.ok) {
            posServerStatus = 'OK';
            posServerDetail = `Health Check (Code: ${response.status} OK)`;
            //        .
            await response.json(); 
        } else {
            posServerStatus = 'ERROR';
            posServerDetail = `   (Code: ${response.status})`;
        }
    } catch (error) {
        //       
        posServerStatus = 'ERROR';
        posServerDetail = `  : ${error instanceof Error ? error.message : ' '}`;
    }


//  Mock API    
const simulatedData: DeviceCheckData = {
        network: [
            { label: '  (WAN)', status: 'OK', detail: 'Public IP  ' },
            //  API     
            { label: 'POS  ', status: posServerStatus, detail: posServerDetail },
            { label: 'VAN/ ', status: 'WARN', detail: '   (1500ms)' },
        ],
        payment: [
            { label: ' /IC', status: 'OK', detail: 'USB  ' },
            { label: ' ', status: 'ERROR', detail: '   ( )' },
        ],
        printer: [
            { label: ' ', status: 'WARN', detail: '   (20% )' },
            { label: '  ()', status: 'OK', detail: '   ' },
        ],
        system: [
            { label: 'CPU/ ', status: 'OK', detail: 'CPU: 25%, RAM: 60%' },
            { label: '  ', status: 'OK', detail: ' : 50GB' },
            { label: ' ', status: 'OK', detail: 'v1.2.5' },
            { label: '  ', status: 'WARN', detail: '  1 (10 )' },
        ],
    };
    
    return simulatedData;
};

export const useDeviceStatus = () => {
    const [statusData, setStatusData] = useState<DeviceCheckData>(initialData);
    const [isLoading, setIsLoading] = useState(false);
    const [lastChecked, setLastChecked] = useState<Date | null>(null);

    const runDeviceCheck = useCallback(async () => {
        setIsLoading(true);
        try {
            //  : fetchCheckDevice  
            const data = await fetchCheckDevice(); 
            setStatusData(data);
            setLastChecked(new Date());
        } catch (error) {
            console.error("    :", error);
            //      ERROR  
        } finally {
            setIsLoading(false);
        }
    }, []);

    
    //     
    useEffect(() => {
        runDeviceCheck();
    }, [runDeviceCheck]);

    return {
        statusData,
        isLoading,
        lastChecked,
        runDeviceCheck, //       
    };
};

