// src/pages/AdminScreen.tsx

import React, { useState, useCallback } from 'react';
import styled from 'styled-components';
import { AdminScreenProps, ScreenStates } from '../types';
import { COLORS } from '../theme/colorPalette'; 
import { AdminSalesReport } from './AdminSalesReport';
//  1. AdminProductManagement  
import { AdminProductManagement } from './AdminProductManagement'; 
//  2. AdminStaffPage    
import AdminStaffPage from './AdminStaffPage'; 
import { usePosSystem } from '../hooks/usePosSystem';
import { DeviceCheckScreen } from './DeviceCheckScreen';
import { SystemControlModal, SystemActionType } from '../components/SystemControlModal';

// 3.  AdminSubScreen  STAFF_MANAGEMENT     
type AdminSubScreen = 'MAIN_MENU' | 'SALES_REPORT' | 'PRODUCT_MANAGEMENT' | 'STAFF_MANAGEMENT' | 'ORDER_RESET' | 'DEVICE_CHECK' | 'KIOSK_RESTART';

// ---   ---

const AdminContainer = styled.div`
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: ${COLORS.BACKGROUND};
    color: ${COLORS.TEXT_DARK};
    padding: 30px;
`;

const Header = styled.header`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    border-bottom: 2px solid ${COLORS.PRIMARY};
    padding-bottom: 20px;
`;

const Title = styled.h1`
    font-size: 2.5rem;
    font-weight: bold;
    color: ${COLORS.PRIMARY_DARK || COLORS.PRIMARY}; 
`;

const MenuGrid = styled.main`
    flex-grow: 1;
    display: grid;
    // 3  
    grid-template-columns: repeat(3, 1fr); 
    gap: 30px;
    padding: 20px 0;
`;

const AdminMenuButton = styled.button`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 40px 20px;
    border: none;
    border-radius: 15px;
    font-size: 1.8rem;
    font-weight: 700;
    cursor: pointer;
    transition: transform 0.2s, box-shadow 0.2s;
    background-color: ${COLORS.TEXT_LIGHT}; //  
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    color: ${COLORS.TEXT_DARK};
    
    &:hover {
        transform: translateY(-5px);
        box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    }
`;

const BackButton = styled.button`
    background-color: ${COLORS.DANGER || '#CC0000'};
    color: ${COLORS.TEXT_LIGHT};
    padding: 15px 30px;
    font-size: 1.4rem;
    font-weight: bold;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s;
    
    &:hover {
        background-color: ${COLORS.DANGER_DARK || '#A30000'};
    }
`;

// ---   ---

export const AdminScreen: React.FC<AdminScreenProps> = ({ navigateTo }) => {

    const [subScreen, setSubScreen] = useState<AdminSubScreen>('MAIN_MENU'); 
    const [showSystemModal, setShowSystemModal] = useState(false); //   
    //  []   : KIOGK_RESTART   
    const handleShowSystemModal = () => {
        setShowSystemModal(true);
    };

    //  []      
    const handleSystemAction = async (actionType: SystemActionType) => { //  async 
        setShowSystemModal(false); //  

        // 3.  (CANCEL)      
        if (actionType === 'CANCEL') {
            console.log("  .");
            return;
        }

        // 1.  (RESTART) 
       if (actionType === 'RESTART') {
            const isConfirmed = window.confirm(" :   . ?");
            if (isConfirmed) {
                try {
                    console.log(" [KIOSK RESTART]    (Mock API).");
                    
                    //  Mock API   
                    await new Promise((resolve, reject) => {
                        setTimeout(() => {
                            // 5%    (API    )
                            if (Math.random() < 0.05) { 
                                reject(new Error("    (Mock).")); 
                            } else {
                                resolve(true);
                            }
                        }, 1500); // 1.5   
                    });
                    //  Mock API   
                    
                    resetAllOrderStates(); 
                    navigateTo(ScreenStates.START);
                    alert("  .");
                } catch (error) {
                    // Mock    API    
                    console.error("   :", error);
                    alert(`  .  .`);
                    handleSubNavigate('MAIN_MENU'); 
                }
            }
        }
        // 2.   (SHUTDOWN) 
       else if (actionType === 'SHUTDOWN') {
            const isConfirmed = window.confirm("  :    . ?");
            if (isConfirmed) {
                try {
                    console.log(" [KIOSK SHUTDOWN]     (Mock API).");

                    //  Mock API   
                    await new Promise((resolve, reject) => {
                        setTimeout(() => {
                             //        (0%)
                             resolve(true);
                        }, 1000); // 1   
                    });
                    //  Mock API   
                    
                    resetAllOrderStates(); 
                    navigateTo(ScreenStates.START); //   
                    alert("   .");

                } catch (error) {
                    console.error("    :", error);
                    alert(`    .  .`);
                }
            }
        }
    };

    const handleSubNavigate = useCallback((targetScreen: AdminSubScreen) => {
        setSubScreen(targetScreen);
    }, []);

    const handleBackToMain = useCallback(() => {
        //       .
        navigateTo(ScreenStates.START); 
    }, [navigateTo]);

    //  [] usePosSystem    
    const { resetAllOrderStates } = usePosSystem();

    //  []     
    const handleOrderReset = useCallback(async () => {
        // 1. window.confirm   2  
        const isConfirmed = window.confirm(
            " :      (,   ) .     .  ?"
        );

       if (isConfirmed) {
        try {
            //  [API  ]      
            //   : await PosApi.forceResetOrders(); 
            
            // Mock API  
            const apiResponse = await fetch('http://localhost:3001/transactions', { 
                method: 'DELETE' // DELETE      
            });

            if (!apiResponse.ok && apiResponse.status !== 404) {
                 // 404     ,  (500 ) 
                 throw new Error("   .");
            }
            
            // 2.   
            resetAllOrderStates(); 
            
            console.log(" [ORDER RESET]      .");
            alert("      .");

        } catch (error) {
            console.error("   API  :", error);
            alert(`   .  .`);
        }
    }
}, [resetAllOrderStates]);// resetAllOrderStates usePosSystem   
 

    //    : '   ' handleSubNavigate  
    const adminMenus = [
       { key: 'SALES_REPORT', label: '/  ', action: () => handleSubNavigate('SALES_REPORT') },
       { 
           key: 'PRODUCT_MANAGEMENT', 
           label: '/  ', 
           action: () => handleSubNavigate('PRODUCT_MANAGEMENT') 
       },
       { 
           key: 'STAFF_MANAGEMENT', 
           label: '   ', 
           //  'STAFF_MANAGEMENT'   
           action: () => handleSubNavigate('STAFF_MANAGEMENT') 
        }, 
        
       { key: 'DEVICE_CHECK', label: '  ', action: () => handleSubNavigate('DEVICE_CHECK') },
       { key: 'ORDER_RESET', label: '  ', action: handleOrderReset },
       
       { key: 'KIOSK_RESTART', label: '  (/)', action: handleShowSystemModal },
    ];
    
    //  4.      
    
    //    
    if (subScreen === 'SALES_REPORT') {
        return (
            <AdminSalesReport 
                navigateTo={() => handleSubNavigate('MAIN_MENU')} 
            />
        );
    }
    
    //    
    if (subScreen === 'PRODUCT_MANAGEMENT') {
        return (
            <AdminProductManagement 
                navigateTo={() => handleSubNavigate('MAIN_MENU')} 
            />
        );
    }
    
    //      
    if (subScreen === 'STAFF_MANAGEMENT') {
        return (
            <AdminStaffPage 
                // AdminStaffPage 'navigateTo' prop  ,     .
                navigateTo={() => handleSubNavigate('MAIN_MENU')} 
            />
        );
    }
        //       () 
    if (subScreen === 'DEVICE_CHECK') {
        return (
        <DeviceCheckScreen 
            navigateTo={() => handleSubNavigate('MAIN_MENU')} 
        />
    );
    }
    
    //    ( )  MAIN_MENU .

    // 5.     
    return (
        <AdminContainer>
            <Header>
                <Title>  </Title>
                <BackButton onClick={handleBackToMain}>
                        
                </BackButton>
            </Header>

            <MenuGrid>
                {adminMenus.map(menu => (
                    <AdminMenuButton key={menu.key} onClick={menu.action}>
                        {menu.label}
                    </AdminMenuButton>
                ))}
            </MenuGrid>

            {/*          */}
            {showSystemModal && <SystemControlModal onAction={handleSystemAction} />}
            
        </AdminContainer>
    );
};