// src/components/admin/staff/StaffEditModal.tsx -     

import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { COLORS } from '../../../theme/colorPalette';
import { StaffUser, PermissionRole } from '../../../types/admin';

// ---   (StaffAddModal.tsx  ) ---
const ModalOverlay = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
`;

const ModalContent = styled.div`
    background-color: ${COLORS.BACKGROUND_LIGHT};
    padding: 30px;
    border-radius: 12px;
    width: 450px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
`;

const ModalTitle = styled.h2`
    color: ${COLORS.PRIMARY_DARK};
    margin-bottom: 25px;
    border-bottom: 2px solid ${COLORS.BACKGROUND_MEDIUM};
    padding-bottom: 10px;
`;

const FormGroup = styled.div`
    margin-bottom: 20px;
`;

const Label = styled.label`
    display: block;
    color: ${COLORS.TEXT_DARK};
    font-weight: bold;
    margin-bottom: 8px;
`;

const Input = styled.input`
    width: 100%;
    padding: 10px;
    border: 1px solid ${COLORS.BACKGROUND_DARK};
    border-radius: 6px;
    font-size: 1rem;
    color: ${COLORS.TEXT_DARK};
    background-color: ${COLORS.BACKGROUND};
    &:focus {
        border-color: ${COLORS.ACCENT};
        outline: none;
    }
`;

const Select = styled.select`
    width: 100%;
    padding: 10px;
    border: 1px solid ${COLORS.BACKGROUND_DARK};
    border-radius: 6px;
    font-size: 1rem;
    color: ${COLORS.TEXT_DARK};
    background-color: ${COLORS.BACKGROUND_LIGHT};
    cursor: pointer;
`;

const ButtonGroup = styled.div`
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 30px;
`;

const CancelButton = styled.button`
    padding: 10px 20px;
    background-color: ${COLORS.TEXT_MUTED};
    color: ${COLORS.TEXT_LIGHT};
    border: none;
    border-radius: 8px;
    cursor: pointer;
    &:hover {
        background-color: ${COLORS.TEXT_DARK};
    }
`;

const SaveButton = styled.button`
    padding: 10px 20px;
    background-color: ${COLORS.ACCENT};
    color: ${COLORS.TEXT_LIGHT};
    border: none;
    border-radius: 8px;
    cursor: pointer;
    &:disabled {
        background-color: ${COLORS.BACKGROUND_DARK};
        cursor: not-allowed;
    }
    &:hover:not(:disabled) {
        background-color: ${COLORS.ACCENT_DARK};
    }
`;

// ---   ---

const permissionRoles: PermissionRole[] = ['Admin', 'Manager', 'Staff'];

interface StaffEditModalProps {
    isOpen: boolean;
    onClose: () => void;
    staff: StaffUser | null; //  StaffUser 
    onSave: (updatedUser: StaffUser) => void;
    onToggleActive: (userId: string, isActive: boolean) => void; // /  
}

export const StaffEditModal: React.FC<StaffEditModalProps> = ({ isOpen, onClose, staff, onSave, onToggleActive }) => {
    
    const [formData, setFormData] = useState<StaffUser | null>(staff);

    useEffect(() => {
        setFormData(staff);
    }, [staff]);

    //      (TS7006 )
    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
        if (!formData) return;
        const { name, value } = e.target;
        setFormData(prev => prev ? {
            ...prev,
            [name]: value,
        } : null);
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        if (formData) {
            onSave(formData);
            //    onSave     .
            // onClose(); // AdminStaffPage onSave    
        }
    };

    const handleToggle = () => {
        if (!formData) return;
        const newActiveState = !formData.isActive;
        
        if (window.confirm(`${formData.name}  ${newActiveState ? '' : ''} ?`)) {
            onToggleActive(formData.id, newActiveState);
            //     (API   )
            setFormData(prev => prev ? { ...prev, isActive: newActiveState } : null); 
        }
    };

    if (!isOpen || !formData) {
        return null;
    }

    return (
        <ModalOverlay onClick={onClose}>
            {/*      (TS7006 ) */}
            <ModalContent onClick={(e: React.MouseEvent) => e.stopPropagation()}> 
                <ModalTitle>      ({formData.name})</ModalTitle>
                <form onSubmit={handleSubmit}>
                    
                    {/* 1.   */}
                    <FormGroup>
                        <Label htmlFor="name"> </Label>
                        <Input
                            id="name"
                            name="name"
                            type="text"
                            value={formData.name}
                            onChange={handleChange}
                        />
                    </FormGroup>
                    
                    {/* 2.  ( ) */}
                    <FormGroup>
                        <Label htmlFor="employeeId"> ( ID) [ ]</Label>
                        <Input
                            id="employeeId"
                            name="employeeId"
                            type="text"
                            value={formData.employeeId}
                            readOnly
                            style={{ backgroundColor: COLORS.BACKGROUND_MEDIUM }}
                        />
                    </FormGroup>

                    {/* 3.    */}
                    <FormGroup>
                        <Label htmlFor="role"> </Label>
                        <Select
                            id="role"
                            name="role"
                            value={formData.role}
                            onChange={handleChange}
                        >
                            {permissionRoles.map(role => (
                                <option key={role} value={role}>{role}</option>
                            ))}
                        </Select>
                    </FormGroup>
                    
                    {/* 4.  /   */}
                    <FormGroup style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderTop: `1px solid ${COLORS.BACKGROUND_MEDIUM}`, paddingTop: '15px' }}>
                        <Label style={{ margin: 0, fontWeight: 'normal' }}>
                             : <span style={{ color: formData.isActive ? COLORS.ACCENT_DARK : COLORS.TEXT_MUTED }}>{formData.isActive ? '' : ''}</span>
                        </Label>
                        <SaveButton 
                            type="button" 
                            onClick={handleToggle}
                            style={{ backgroundColor: formData.isActive ? COLORS.DANGER : COLORS.ACCENT }}
                        >
                            {formData.isActive ? ' ' : ' '}
                        </SaveButton>
                    </FormGroup>

                    <ButtonGroup>
                        <CancelButton type="button" onClick={onClose}></CancelButton>
                        <SaveButton type="submit"> </SaveButton>
                    </ButtonGroup>
                </form>
            </ModalContent>
        </ModalOverlay>
    );
};