//src/components/AdminCategoryModal.tsx

import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { AdminCategoryItem } from '../types/admin';
import { Category } from '../types';
import { COLORS } from '../theme/colorPalette';


const initialNewCategory: Omit<AdminCategoryItem, 'id' | 'kioskOrder'> = {
    name: ' ' as Category, 
    isVisible: true,
};


// ---   (AdminItemModal   ) ---

const ModalOverlay = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1010; //    
`;

const ModalContent = styled.div`
    background: ${COLORS.BACKGROUND_LIGHT};
    padding: 30px;
    border-radius: 12px;
    width: 90%;
    max-width: 400px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
`;

const Title = styled.h3`
    color: ${COLORS.PRIMARY_DARK};
    margin-bottom: 25px;
    font-size: 1.5rem;
    border-bottom: 2px solid ${COLORS.TEXT_MUTED};
    padding-bottom: 10px;
`;

const FormRow = styled.div`
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    label {
        font-weight: bold;
        width: 100px;
        color: ${COLORS.TEXT_DARK};
    }
    input[type="text"] {
        flex-grow: 1;
        padding: 10px;
        border: 1px solid ${COLORS.TEXT_MUTED};
        border-radius: 6px;
        font-size: 1rem;
    }
    input[type="checkbox"] {
        width: auto;
        margin-left: 0;
        margin-right: 10px;
    }
`;

const ButtonGroup = styled.div`
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
    gap: 10px;
    button {
        padding: 10px 20px;
        border: none;
        border-radius: 8px;
        font-size: 1rem;
        cursor: pointer;
    }
`;

const SaveButton = styled.button`
    background-color: ${COLORS.ACCENT};
    color: ${COLORS.TEXT_LIGHT};
    &:hover { background-color: ${COLORS.ACCENT_DARK}; }
`;

const CancelButton = styled.button`
    background-color: ${COLORS.TEXT_MUTED};
    color: ${COLORS.TEXT_DARK};
    &:hover { background-color: ${COLORS.BACKGROUND_DARK}; }
`;

const DeleteButton = styled.button`
    background-color: ${COLORS.DANGER};
    color: ${COLORS.TEXT_LIGHT};
    margin-right: auto; /*   */
    &:hover { background-color: ${COLORS.DANGER_DARK}; }
`;

// ---   ---

interface AdminCategoryModalProps {
    // item  '' ,  '' 
    categoryToEdit: AdminCategoryItem | null; 
    onClose: () => void;
    
 //  []   : onSave     AdminCategoryItem .
    onSave: (category: AdminCategoryItem) => void; 
    
    onAdd: (name: Category) => void; //    ( )
    onDelete: (categoryId: string) => void;
}

// ... (initialNewCategory, useState  ) ...


export const AdminCategoryModal: React.FC<AdminCategoryModalProps> = ({ 
    categoryToEdit, 
    onClose, 
    //  onSave  onAdd, onSaveToEdit   
    onSave, //         .
    onDelete,
    onAdd //     
}) => {
    // ... (isEditMode, categoryData, handleChange  ) ...
    const isEditMode = categoryToEdit !== null;

    const [categoryData, setCategoryData] = useState<Omit<AdminCategoryItem, 'id' | 'kioskOrder'>>(
        isEditMode 
            ? { name: categoryToEdit.name, isVisible: categoryToEdit.isVisible }
            : initialNewCategory
    );

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value, checked, type } = e.target;
        
        setCategoryData(prev => ({
            ...prev,
            [name]: type === 'checkbox' ? checked : value,
        }));
    };

   const handleSubmit = () => {
        if (!categoryData.name) {
            alert('  .');
            return;
        }

        if (isEditMode) {
            //  1.  : AdminCategoryItem  onSave( )  
            const finalCategory: AdminCategoryItem = {
                ...categoryToEdit!, 
                name: categoryData.name,
                isVisible: categoryData.isVisible,
            };
            onSave(finalCategory); //  onSave (  )
            
        } else {
            //  2.  : Category (string) onAdd( )  
            //   useAdminData addCategory(name)   .
            onAdd(categoryData.name as Category);
        }
        
        onClose();
    };

    const handleDelete = () => {
        if (isEditMode && window.confirm(`  "${categoryToEdit!.name}"() ?        .`)) {
            onDelete(categoryToEdit!.id);
            onClose();
        }
    };
    
    //   
    const modalTitle = isEditMode ? '  ' : '  ';
    const saveButtonLabel = isEditMode ? '' : '';
    
    return (
        <ModalOverlay onClick={onClose}>
            <ModalContent onClick={e => e.stopPropagation()}>
                <Title>{modalTitle}</Title>
                
                <form onSubmit={e => e.preventDefault()}>
                    <FormRow>
                        <label htmlFor="name"> </label>
                        <input 
                            type="text" 
                            id="name"
                            name="name" 
                            value={categoryData.name} 
                            onChange={handleChange} 
                            // Category      string 
                            maxLength={20}
                        />
                    </FormRow>
                    
                    <FormRow>
                        <label htmlFor="isVisible"> </label>
                        <input 
                            type="checkbox" 
                            id="isVisible"
                            name="isVisible" 
                            checked={categoryData.isVisible} 
                            onChange={handleChange} 
                        />
                        <span>{categoryData.isVisible ? ' ' : ''}</span>
                    </FormRow>

                    <ButtonGroup>
                        {isEditMode && <DeleteButton type="button" onClick={handleDelete}> </DeleteButton>}
                        <CancelButton type="button" onClick={onClose}></CancelButton>
                        {/*  ''   handleSubmit    (  )  */}
                        <SaveButton type="button" onClick={handleSubmit}>{saveButtonLabel}</SaveButton>
                    </ButtonGroup>
                </form>

            </ModalContent>
        </ModalOverlay>
    );
};