import React from 'react';

//      (\n) HTML <br />    .
export const renderTextWithBreaks = (text: string) => {
 return text.split('\n').map((line, index, array) => (
  <React.Fragment key={index}>
   {line}
   {/*     <br />   . */}
   {index < array.length - 1 && <br />}
  </React.Fragment>
 ));
};