// src/pages/StartScreen.tsx
import React, { useState } from 'react';
import styled from 'styled-components';
import { KioskButton } from '../components/UI/KioskButton';
import { LanguageToggle } from '../components/UI/LanguageToggle';
import { COLORS } from '../theme/colorPalette';
// import { useScreenNav } from '../hooks/useScreenNav';
import { useLanguage } from '../components/LanguageContext';
import { useTime } from '../hooks/useTime';
import { OrderType, ScreenState, ScreenStates } from '../types';
import { renderTextWithBreaks } from '../utils/textUtils';
import { usePosSystem } from '../hooks/usePosSystem'; //
import { AdminAuthModal } from '../components/AdminAuthModal';




//  : #008080 ( ) 
const StartScreenContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: ${COLORS.PRIMARY};
  color: ${COLORS.TEXT_LIGHT};
  padding: 40px;
`;

const Header = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 80px;
`;

const AdminButton = styled.button`
  background: none;
  border: 2px solid ${COLORS.TEXT_LIGHT};
  color: ${COLORS.TEXT_LIGHT};
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
`;

const RightControls = styled.div`
  display: flex;
  align-items: center;
  gap: 20px;
`;

const TimeDisplay = styled.div`
  font-size: 1.4rem;
  font-weight: 500;
  min-width: 150px;
  text-align: right;
`;

const MainContent = styled.main`
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
`;

const WelcomeMessage = styled.h1`
  font-size: 5rem;
  font-weight: 800;
  margin-bottom: 220px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  line-height: 1.5;
  white-space: pre-wrap;
  text-align: center; /*      */
`;

const Footer = styled.footer`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom:60px;
`;


//  StartScreenProps  : navigateTo    
interface StartScreenProps {
  navigateTo: (screen: ScreenState, props?: any) => void;
}

export const StartScreen: React.FC<StartScreenProps> = ({ navigateTo }) => {
//   const { navigateTo } = useScreenNav(); //  navigateTo 
  const { t } = useLanguage();
  const { formattedTime } = useTime();
  

   //  [  1] usePosSystem setOrderTypeState  . 
  const { setOrderTypeState } = usePosSystem();

  //  []     
    const [isAdminModalOpen, setIsAdminModalOpen] = useState(false);

  const handleAdminClick = () => {
    //         
    setIsAdminModalOpen(true);
  };

  //  []    AdminScreen   
    const handleAdminAuthSuccess = () => {
        setIsAdminModalOpen(false); //  
        console.log("[ADMIN]   .   .");
        // AdminScreen  ( AdminScreen   ScreenStates  )
        navigateTo(ScreenStates.ADMIN); 
    };

  const handleOrderTypeSelect = (type: OrderType) => {
    console.log(`[ORDER]   : ${type}`);
    //      (useOrder  )
   //  [  2] setOrderTypeState    . 
    setOrderTypeState(type);
    
  navigateTo(ScreenStates.MAIN, { orderType: type })
 };


  return (
    <StartScreenContainer>
      <Header>
        {/*   :   */}
        <AdminButton onClick={handleAdminClick}>
          {t('admin_mode')}
        </AdminButton>

        {/*   :     */}
        <RightControls>
          <LanguageToggle />
          <TimeDisplay>{formattedTime}</TimeDisplay>
        </RightControls>
      </Header>

      <MainContent>
        {/* :   */}
        <WelcomeMessage>
          {renderTextWithBreaks(t('welcome_message'))}
        </WelcomeMessage>
      </MainContent>

      <Footer>
        {/*  :   (Primary -   ) */}
        <KioskButton
          variant="accent"
          onClick={() => handleOrderTypeSelect('STORE')}
        >
          {t('store')}
        </KioskButton>

        {/*  :   (Accent -  ) */}
        <KioskButton
          variant="accent"
          onClick={() => handleOrderTypeSelect('TAKEOUT')}
        >
          {t('takeout')}
        </KioskButton>
      </Footer>

      {/*  [ ] StartScreenContainer     */}
            {isAdminModalOpen && (
                <AdminAuthModal
                    onClose={() => setIsAdminModalOpen(false)}
                    onAuthSuccess={handleAdminAuthSuccess}
                />
            )}
    </StartScreenContainer>
  );
};