// src/pages/AdminStaffPage.tsx

import React, { useState, useEffect, useCallback } from 'react';
import styled from 'styled-components';
import { COLORS } from '../theme/colorPalette';
import { StaffUser, PermissionRole, StaffForm } from '../types/admin'; //  PermissionRole  StaffUser  
import { StaffList } from '../components/admin/staff/StaffList';
import { StaffAddModal } from '../components/admin/staff/StaffAddModal';
import { StaffEditModal } from '../components/admin/staff/StaffEditModal'; //   StaffEditModal  

//  API  
import { fetchStaffList, addStaffUser, updateStaffInfo, toggleStaffActive } from '../api/admin/staffApi'; 
//   :        .


// ---   ---
const PageContainer = styled.div`
    padding: 40px;
    background-color: ${COLORS.BACKGROUND_LIGHT};
    min-height: 100vh;
`;

const Header = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    border-bottom: 2px solid ${COLORS.BACKGROUND_DARK};
    padding-bottom: 15px;
`;

const Title = styled.h1`
    color: ${COLORS.PRIMARY_DARK};
    font-size: 2rem;
`;

const AddButton = styled.button`
    padding: 10px 20px;
    background-color: ${COLORS.ACCENT};
    color: ${COLORS.TEXT_LIGHT};
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    cursor: pointer;    
    &:hover {
        background-color: ${COLORS.ACCENT_DARK};
    }
`;

const BackButton = styled.button`
    padding: 10px 20px;
    background-color: ${COLORS.DANGER};
    color: ${COLORS.TEXT_LIGHT};
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    &:hover { background-color: ${COLORS.DANGER_DARK}; }
`;

const ActionButton = styled.button`
    padding: 5px 10px;
    margin-left: 5px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    background-color: ${COLORS.PRIMARY_DARK}; 
    color: ${COLORS.TEXT_LIGHT};
    font-size: 0.8rem;

    &:hover {
        opacity: 0.8;
    }
`;
interface AdminStaffPageProps {
    navigateTo: () => void; // AdminScreen   .
}

// ---   ---
const AdminStaffPage: React.FC<AdminStaffPageProps> = ({ navigateTo }) => {
    const [staffList, setStaffList] = useState<StaffUser[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [isAddModalOpen, setIsAddModalOpen] = useState(false);
    const [isEditModalOpen, setIsEditModalOpen] = useState(false);
    const [selectedStaff, setSelectedStaff] = useState<StaffUser | null>(null);

    //     (API ) 
    const loadStaffs = useCallback(async () => {
        setIsLoading(true);
        try {
            //  API  fetchStaffList  
            const data = await fetchStaffList(); 
            setStaffList(data);
        } catch (error) {
            console.error("   :", error);
            alert("    .");
        } finally {
            setIsLoading(false);
        }
    }, []);

    useEffect(() => {
        loadStaffs();
    }, [loadStaffs]);

    //     (StaffList      )
    const handleUpdateRole = useCallback(async (userId: string, newRole: PermissionRole) => {
        const userToUpdate = staffList.find(u => u.id === userId);
        if (!userToUpdate) return;
        
        try {
            //  Role   (API updateStaffInfo )
            const updatedUser = { ...userToUpdate, role: newRole };
            await updateStaffInfo(updatedUser); 
            
            setStaffList(prevList => prevList.map(user => 
                user.id === userId ? updatedUser : user
            ));
            
        } catch (error) {
            console.error("  :", error);
            alert("  .");
        }
    }, [staffList]);


    //      (API ) 
    const handleSaveNewStaff = useCallback(async (formData: StaffForm) => {
        try {
            const newUser = await addStaffUser(formData);
            setStaffList(prevList => [...prevList, newUser]);
            setIsAddModalOpen(false);
            alert(`${newUser.name}   .`);
        } catch (error) {
            console.error("  :", error);
            alert("   .");
        }
    }, []);


    //     
    const handleOpenEditModal = useCallback((staff: StaffUser) => {
        setSelectedStaff(staff);
        setIsEditModalOpen(true);
    }, []);

    //     
    const handleSaveEditedStaff = useCallback(async (updatedUser: StaffUser) => {
        try {
            await updateStaffInfo(updatedUser);
            
            setStaffList(prevList => prevList.map(user => 
                user.id === updatedUser.id ? updatedUser : user
            ));
            setIsEditModalOpen(false);
            alert(`${updatedUser.name}   .`);
            
        } catch (error) {
            console.error("  :", error);
            alert("  .");
        }
    }, []);
    
    //   /  (API ) 
    const handleToggleActive = useCallback(async (userId: string, isActive: boolean) => {
        try {
            await toggleStaffActive(userId, isActive);
            
            setStaffList(prevList => prevList.map(user => 
                user.id === userId ? { ...user, isActive: isActive } : user
            ));
            
        } catch (error) {
            console.error("  :", error);
            alert("   .");
        }
    }, []);


    if (isLoading) {
        return <PageContainer><p>   ...</p></PageContainer>;
    }

    return (
        <PageContainer>
            <Header>
                <Title>    </Title>
                {/* 2.           */}
                <div style={{ display: 'flex', alignItems: 'center', gap: '20px' }}>
                    
                    {/* AddButton */}
                    <AddButton onClick={() => setIsAddModalOpen(true)}>
                        +   
                    </AddButton>

                    {/* BackButton */}
                    <BackButton onClick={navigateTo}>
                          
                    </BackButton>
                </div>

            </Header>

            <StaffList 
                staffs={staffList} 
                onUpdateRole={handleUpdateRole}
                onEditStaff={handleOpenEditModal}
                //  onToggleActive prop   
                onToggleActive={handleToggleActive} 
            />
            <StaffAddModal 
                isOpen={isAddModalOpen} 
                onClose={() => setIsAddModalOpen(false)} 
                onSave={handleSaveNewStaff} 
            />
            
            <StaffEditModal //  StaffEditModal   
                isOpen={isEditModalOpen}
                onClose={() => setIsEditModalOpen(false)}
                staff={selectedStaff} 
                onSave={handleSaveEditedStaff}
                onToggleActive={handleToggleActive}
            />
        </PageContainer>
    );
};

export default AdminStaffPage;